@import "../../global/utils";
@import "../helpers/colors";

html {
  font-size: 100%;
}

body {
  background-color: $color-gray-10;
  color: $color-gray-60;
  font: normal 1em/1.7 $tdc-fontstack;
  box-sizing: border-box;
  margin: 0;
  -webkit-font-smoothing: antialiased;
}

p,
h1,
h2,
h3,
h4,
h5,
h6,
figure,
ul,
ol,
li,
fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

q:before,
q:after {
 content: "";
}

img {
  max-width: 100%;
  height: auto;
  border: 0;
  margin: 0;
  padding: 0;
}

a {
  color: $color-link;
  text-decoration: none;
  transition: color .3s ease-in-out;

  &:hover,
  &:focus {
    color: $color-link-hover;
  }
}

button {
  border: 0;
  cursor: pointer;
  background: none;
  margin: 0;
  padding: 0;
  outline: none;
}

// Removes the default Chrome reset button on search fields
input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none !important;
}

// Removes the deafult IE reset button
::-ms-clear, ::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

//Added to content div or body when an overlay or mobile menu is open
.noScroll {
  overflow: hidden;
  position: fixed;
}
