/////////////////
// Semantic.gs // for SCSS: http://sass-lang.com/
/////////////////

@import "mq";

// Defaults which can be overridden in the local site's settings.scss file.
@if (variable-exists(column-width) == false) {
  $column-width: 64px;
}

@if (variable-exists(gutter-width) == false) {
  $gutter-width: 24px;
}
@if (variable-exists(columns) == false) {
  $columns: 12;
}
@if (variable-exists(total-width) == false) {
  $total-width: 100%;
}

// Utility function - you should never need to modify this
@function gridsystem-width($columns:$columns) {
	@return ($column-width * $columns) + ($gutter-width * $columns);
}

// margin equal to half a gutter space
$gridMargin: $total-width*(($gutter-width*.5)/gridsystem-width($columns));

// The micro clearfix http://nicolasgallagher.com/micro-clearfix-hack/
@mixin clearfix() {
	&:before,
	&:after {
		content: "";
		display: table;
	}

	&:after {
		clear: both;
	}
}

//////////
// GRID //
//////////
@mixin row($columns:$columns) {
	display: block;
	width: $total-width*(($gutter-width + gridsystem-width($columns))/gridsystem-width($columns));
	margin: 0 $total-width*((($gutter-width*.5)/gridsystem-width($columns))*-1);
	// *width: $total-width*(($gutter-width + gridsystem-width($columns))/gridsystem-width($columns))-$correction;
	// *margin: 0 $total-width*((($gutter-width*.5)/gridsystem-width($columns))*-1)-$correction;
	@include clearfix();
}

@mixin column($x, $columns:$columns) {
	display: inline;
	float: left;
	width: $total-width*(((($gutter-width+$column-width)*$x)-$gutter-width) / gridsystem-width($columns));
	margin: 0 $total-width*(($gutter-width*.5)/gridsystem-width($columns));
}

@mixin simplecolumn($x, $columns:$columns) {
	width: $total-width*(((($gutter-width+$column-width)*$x)-$gutter-width) / gridsystem-width($columns));
}

@mixin flexcolumn($x, $columns:$columns) {
	flex-basis: $total-width*(((($gutter-width+$column-width)*$x)-$gutter-width) / gridsystem-width($columns));
}

// this only returns the width value!
@function columnwidth($x, $columns:$columns) {
  @return $total-width*(((($gutter-width+$column-width)*$x)-$gutter-width) / gridsystem-width($columns));
}

// the move-left and move-right mixins let's you rearrange the columns of the grid
@mixin move-left($offset: 1) {
  left: ($total-width*((($gutter-width+$column-width)*$offset) / gridsystem-width($columns)) + $total-width*(($gutter-width*.5)/gridsystem-width($columns)) - 1);
}

@mixin move-right($offset: 1) {
  right: ($total-width*((($gutter-width+$column-width)*$offset) / gridsystem-width($columns)) + $total-width*(($gutter-width*.5)/gridsystem-width($columns)) - 1);
}

// the push and pull mixins adds margin/whitespace to your columns
@mixin push($offset: 1) {
 margin-left: $total-width*((($gutter-width+$column-width)*$offset) / gridsystem-width($columns)) + $total-width*(($gutter-width*.5)/gridsystem-width($columns));
}

@mixin pull($offset: 1) {
 margin-right: $total-width*((($gutter-width+$column-width)*$offset) / gridsystem-width($columns)) + $total-width*(($gutter-width*.5)/gridsystem-width($columns));
}

// make it possible to inset horizontal padding of size equal to 1-12 col. Sets on both sides!
@mixin gridPadding($columnLeft, $columnRight) {
	padding-left: (($total-width*(((($gutter-width+$column-width)*$columnLeft)-$gutter-width) / gridsystem-width($columns))) + 2*$gridMargin);
	padding-right: (($total-width*(((($gutter-width+$column-width)*$columnRight)-$gutter-width) / gridsystem-width($columns))) + 2*$gridMargin);
}

// make it possible to inset horizontal padding of size equal to 1-12 col. Sets on both sides!
@mixin gridMargin($columnLeft, $columnRight) {
	margin-left: (($total-width*(((($gutter-width+$column-width)*$columnLeft)-$gutter-width) / gridsystem-width($columns))) + 2*$gridMargin);
	margin-right: (($total-width*(((($gutter-width+$column-width)*$columnRight)-$gutter-width) / gridsystem-width($columns))) + 2*$gridMargin);
}

// define grid classes for plug'n'play
.grid__row {
	@include row(12);

	[class*='grid__col--'] {
    display: inline;
    float: left;
    margin: 0 $total-width*(($gutter-width*.5)/gridsystem-width($columns));
	}

	@for $i from 1 through $columns {
    .grid__col--#{$i} {
      @include simplecolumn($i);
    }
  }

  @for $i from 1 through $columns {
    .grid__col--move-left-#{$i} {
      @include move-left($i);
      position: relative;
    }
  }

  @for $i from 1 through $columns {
    .grid__col--move-right-#{$i} {
      @include move-right($i);
      position: relative;
    }
  }

  @for $i from 1 through $columns {
    .grid__col--push-#{$i} {
      @include push($i);
    }
  }

  @for $i from 1 through $columns {
    .grid__col--pull-#{$i} {
      @include pull($i);
    }
  }

  &:not(.grid__row--forced) {
    // all columns stacked and push/pull removed in mobile views
    @include mq($to: s-screen) {
      //mobile
      @for $i from 1 through $columns {
       .grid__col--#{$i} {
          @include simplecolumn(12);
        }
      }

      @for $i from 1 through $columns {
       .grid__col--push-#{$i} {
          @include push(0);
        }
      }

      @for $i from 1 through $columns {
       .grid__col--pull-#{$i} {
          @include pull(0);
        }
      }

      @for $i from 1 through $columns {
        .grid__col--move-left-#{$i} {
          @include move-left(0);
          position: inherit;
        }
      }

      @for $i from 1 through $columns {
        .grid__col--move-right-#{$i} {
          @include move-right(0);
          position: inherit;
        }
      }
    }
  }

  &.grid__row--forced-sub-cols {
    // columns in THIS row will not be stacked in mobile view
    @include mq($to: s-screen) {
      //mobile
      @for $i from 1 through $columns {
        > .grid__col--#{$i} {
          @include simplecolumn($i);
        }
      }
    }
  }
}
