@import "../../global/utils";
@import "../helpers/colors";

.content {
  clear: both;
  transition: transform .3s ease-in-out;

  &.push-left {
    transform: translate3d(-80%, 0, 0);
   // cursor: pointer;

    @include mq($from: s-screen) {
      transform: translate3d(0, 0, 0);
      cursor: default;
    }
  }
}
