@font-face {
	font-family: "TdcCom-iconfont";
	src: url("/build/fonts/TdcCom/TdcCom-iconfont.eot");
	src: url("/build/fonts/TdcCom/TdcCom-iconfont.eot?#iefix") format("embedded-opentype"),
		url("/build/fonts/TdcCom/TdcCom-iconfont.woff") format("woff"),
		url("/build/fonts/TdcCom/TdcCom-iconfont.ttf") format("truetype"),
		url("/build/fonts/TdcCom/TdcCom-iconfont.svg#TdcCom-iconfont") format("svg");
}

@mixin iconfont() {
	&:before, &:after {
		font-family: "TdcCom-iconfont";
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		font-style: normal;
		font-variant: normal;
		font-weight: normal;
		// speak: none; // only necessary if not using the private unicode range (firstGlyph option)
		text-decoration: none;
		text-transform: none;
	}
}

%icon {}

.iconfont {
	@extend %icon;
	@include iconfont();
}

@function icon-char($filename) {
	$char: "";

	@if $filename == chair {
		$char: "\E001";
	}
	@if $filename == document--default {
		$char: "\E002";
	}
	@if $filename == document--pdf {
		$char: "\E003";
	}
	@if $filename == document--webcast {
		$char: "\E004";
	}
	@if $filename == document--xls {
		$char: "\E005";
	}
	@if $filename == facebook {
		$char: "\E006";
	}
	@if $filename == favorite {
		$char: "\E007";
	}
	@if $filename == linkedin {
		$char: "\E008";
	}
	@if $filename == mail {
		$char: "\E009";
	}
	@if $filename == member {
		$char: "\E00A";
	}
	@if $filename == test {
		$char: "\E00B";
	}
	@if $filename == twitter {
		$char: "\E00C";
	}
	@if $filename == ui__arrow--down {
		$char: "\E00D";
	}
	@if $filename == ui__arrow--left {
		$char: "\E00E";
	}
	@if $filename == ui__arrow--right {
		$char: "\E00F";
	}
	@if $filename == ui__arrow--up {
		$char: "\E010";
	}
	@if $filename == ui__check {
		$char: "\E011";
	}
	@if $filename == ui__close {
		$char: "\E012";
	}
	@if $filename == ui__crosslink {
		$char: "\E013";
	}
	@if $filename == ui__minus {
		$char: "\E014";
	}
	@if $filename == ui__nasdaq_arrow_down {
		$char: "\E015";
	}
	@if $filename == ui__nasdaq_arrow_up {
		$char: "\E016";
	}
	@if $filename == ui__play {
		$char: "\E017";
	}
	@if $filename == ui__plus {
		$char: "\E018";
	}
	@if $filename == ui__quote {
		$char: "\E019";
	}
	@if $filename == ui__search {
		$char: "\E01A";
	}

	@return $char;
}

@mixin icon($filename, $insert: before) {
	&:#{$insert} {
		@extend %icon;
		@if $filename == 'document--default' {
      content: icon-char($filename);
    } @else {
      content: icon-char($filename) !important;
    }
	}
}

