@import "../../global/utils";
@import "../helpers/colors";

.button {
  padding: 0 2em;
  margin: 0;
  display: inline-block;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  border: 2px solid transparent;
  border-radius: 3em;
  cursor: pointer;
  outline: none;
  font: 600  .75em/3 $tdc-fontstack;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  white-space: nowrap;
  user-select: none;
  max-width: 100%;
  text-overflow: ellipsis;
  text-decoration: none;

  //Primary
  &--primary {
    background-color: $color-link;
    color: $color-white;

    &:hover,
    &:focus {
      background-color: transparent;
      border: 2px solid $color-link;
      color: $color-link;
    }

    &:active {
      background-color: $color-link-active;
      color: $color-link;
    }

    &.button--bordered {
      border: solid 2px $color-white;
    }
  }

  //Secondary
  &--secondary {
    border: 2px solid $color-link;
    color: $color-link;

    &:hover,
    &:focus {
      background-color: $color-link;
      border-color: $color-link;
      color: $color-white;
    }

    &:active {
      border-color: $color-link-active;
      color: $color-link-active;
    }

    &--white {
      color: $color-white;
      border: 2px solid $color-white;
    }
  }

  //Tertiary
  &--tertiary {
    border: 2px solid $color-white;
    color: $color-white;

    &:hover,
    &:focus {
      background-color: $color-white;
      border-color: $color-white;
      color: $color-link;
    }

    &:active {
      border-color: $color-link-active;
      color: $color-link-active;
    }

    &--white {
      color: $color-white;
      border: 2px solid $color-white;
    }
  }

  //Arrow Link
  &--arrow-link {
    padding: 0;
    font-size: .875em;
    color: $color-link;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: bold;

    @include iconfont();
    @include icon(ui__arrow--right, after);

    &:after {
      font-size: .65rem;
      font-weight: bold;
      margin-left: rem(6);
      display: inline-block;
    }

    &:hover,
    &:focus {

      &:after {
        animation: arrow-right-effect .2s linear;
      }
    }
    
    .no-cssanimations &:hover:after  {
      margin-left: 10px;
    }
  }

  &--connect {
    background-color: $color-link;
    color: $color-white;
    font-size: rem(14);
    letter-spacing: 3px;
    line-height: 3.3em;
    padding: 0 1.2em;
    text-transform: uppercase;
    transition: all .4s ease;

    &:before {
      border-right: 1px solid $color-gray-15;
      content: 'in';
      font-size: rem(18);
      letter-spacing: 0;
      margin-right: .8125em;
      padding-right: .6875em;
      text-transform: lowercase;
      transition: border .3s ease;
    }

    &:hover,
    &:focus {
      background-color: transparent;
      border: 2px solid $color-link;
      color: $color-link;

      &:before {
        border-right: 1px solid $color-link;
      }
    }

    &:active {
      background-color: $color-link-active;
      color: $color-link;
    }
  }
}
