@import "../../global/utils";
@import "../helpers/colors";

$form-height: 50;
$icon-width: 40;
$default-padding-sides: 15;
$font: normal 1.0625em/1.7 $tdc-fontstack;

.forms {
  position: relative;

  &__label {

    &--hidden {
      display: none;
    }
  }

  &__field {
    position: relative;
    margin-bottom: rem(5);
  }

  // ----- Fieldset & Legend styles -----
  legend {
    display: none; // Hide the legend element by default since it's only there for semantic value
  }

  // ----- Input styles -----
  &__input {
    box-sizing: border-box;
    height: rem($form-height);
    width: 100%;
    padding: 0 rem($default-padding-sides);
    font: $font;
    color: $color-gray-60;
    border: 1px solid transparent;

    &:focus {
      outline: 0;
      border: 1px solid $color-gray-30;
    }

    &--search {
      padding-right: rem($icon-width * 2);
    }
  }

  // ----- Submit styles -----
  &__submit {
    height: rem($form-height);
    padding: 0 rem($default-padding-sides);
    font: $font;
    width: 100%;
    color: $color-gray-60;
    background-color: $color-white;
  }

  // ----- Checkbox styles -----
  &__checkbox {

    &[type="checkbox"] {
      display: none;

      & + label span {
        //display: inline-block;
        box-sizing: border-box;
        width: rem(20);
        height: rem(20);
        cursor: pointer;
        background-color: $color-gray-10;
        border: 2px solid $color-blue-50;
        border-radius: rem(2);
        margin-right: rem(16);
        padding-left: rem(2);        
        color: $color-gray-10;
        font-size: rem(14);

        @include iconfont();
        @include icon(ui__check, before);
      }

      &:checked + label span {
        background-color: $color-blue-50;
        border: 2px solid $color-blue-50;
        color: $color-white;
      }
    }

    &--label {
      width: calc(100% - 20px);
      float: left;
      margin-bottom: rem(15);
    }

    &--inline-input {
      border: 2px solid $color-gray-15;
      border-radius: rem(2);
      height: rem(23);
      font-family: 'TDC', arial, helvetica, sans-serif;
      text-align: right;
      padding-right: rem(5);
    }
  }


  // ----- Select styles -----
  &__select {
    display: block;
    height: rem($form-height);

    margin-top: rem(12);

    @include mq($from: m-screen) {
      background-color: $color-white;
    }

    > select {
      position: relative;
      width: 100%;
      height: 100%;
      font: $font;
      color: $color-gray-60;
      background-color: $color-white;
             
      border: 1px solid transparent;
     
      @include mq($from: m-screen) {
        margin: 0;
        padding: 0 rem($icon-width) 0 rem($default-padding-sides);
        background: 0;
        border: 1px solid transparent;
        outline: 0;
        box-sizing: border-box;
        appearance: none;
        z-index: 1;
        border-radius: 0;
        cursor: pointer;

        &:focus{
          outline: 0;
          border: 1px solid $color-gray-30;
        }
      }

      // Remove default styles in IE
      // This is the media query that is needed:
      // @media and (min-width: 880px) and (-ms-high-contrast: active), and (min-width: 880px) and (-ms-high-contrast: none)
      // But mq doesn't as far as i can tell support comma seperated list of rules.
      @include mq($from: m-screen, $and: '(-ms-high-contrast: active)') {

        &::-ms-expand {
          display: none;
        }

        &:focus::-ms-value {
          background: transparent;
          color: $color-gray-60;
        }
      }
      @include mq($from: m-screen, $and: '(-ms-high-contrast: none)') {

        &::-ms-expand {
          display: none;
        }

        &:focus::-ms-value {
          background: transparent;
          color: $color-gray-60;
        }
      }
      // **** End default styles in IE ****

    }

    .forms__icon {
      display: none;

      @include mq($from: m-screen) {
        display: block;
      }
    }
  }

  // ----- Icon styles -----
  &__icon {
    display: table-cell;
    text-align: center;
    position: absolute;
    box-sizing: border-box;
    top: rem(10);
    right: 0;
    line-height: rem($form-height - 20);
    height: rem($form-height - 20);
    width: rem($icon-width);
    border-left: 1px solid $color-gray-15;
    color: $color-blue-40;
    font-size: rem(21);

    &:focus {
      outline: 0;
      border: 1px solid $color-gray-15;
    }

    &--select {
      font-size: rem(12);
    }

    &--close {
      visibility: hidden;
      opacity: 0;
      right: rem($icon-width);
      border: 0;
      color: $color-gray-30;
      transition: all .2s;

      &-visible {
        visibility: visible;
        opacity: 1;
        cursor: pointer;
      }
    }
  }

  // ----- Buttons -----
  &__clear-all {
    text-align: right;
    margin: 0;
    line-height: rem(25);
    height: rem(25);

    .forms__button {

      &:focus {
        outline: 1px solid $color-gray-30;
      }
    }
  }

  // ----- Color modifiers -----
  &--dark {

    .forms__input {
      background-color: $color-gray-50;
      border: 1px solid $color-gray-30;
      color: $color-white;

      &:focus {
        border: 1px solid $color-gray-15;
      }
    }

    .forms__icon {
      border-left: 1px solid $color-gray-30;
      top: 0;
      height: rem($form-height);
      line-height: rem($form-height);
      color: $color-gray-30;

      &--close {
        border: 0;
      }
    }
  }

  &--blue {

    .forms__clear-all {

      .forms__button{
        color: $color-gray-10;
      }
    }
  }
  
  &__errortext {
    color: $color-red-80;
  }
}
