@import '../../global/utils';
@import '../helpers/colors';

.deck {
  @include clearfix;
  padding: 0 rem(20);
  margin-bottom: rem(50);

  @include mq($from: xs-screen) {
    margin-bottom: rem(100);
  }

  @include mq($from: l-screen) {
    margin-bottom: rem(150);
    padding: 0 calc((100% - 75em) / 2);
  }

  @include old-ie {
    width: 75em;
    margin: 0 auto;
  }

  &__header {
    font-weight: bold;
    color: $color-blue-40;
    font-size: 1.875em;
    text-align: center;
    margin-bottom: rem(10);

    .deck--blue &,
    .deck--green & {
      color: $color-white;
    }

    @include mq($from: xs-screen) {
      font-size: 2.5em;
    }
  }

  &__headertext {
    font-weight: 300;
    text-align: center;
    margin-bottom: rem(30);

    .deck--blue &,
    .deck--green & {
      color: $color-white;
    }

    @include mq($from: s-screen) {
      margin-bottom: rem(60);
    }
  }

  &--collapse {
    padding: 0;
    margin-bottom: 0;
  }

  &--collapse-bottom {
    margin-bottom: 0;
  }

  &--margin-top {
    margin-top: rem(50);

    @include mq($from: xs-screen) {
      margin-top: rem(100);
    }

    @include mq($from: l-screen) {
      margin-top: rem(150);
    }
  }

  &--white {
    background: $color-white;
  }

  &--blue {
    background: $color-blue-50;
  }

  &--green {
    background: $color-green-50;
  }

  &--background-image {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
