@import "../../global/utils";
@import "../helpers/colors";

.email-alert-subscription {
  @include column(10);
  @include push(1);

  &__box {
    @include column(12);

    @include mq($from: xs-screen){
      @include column(6);
    }

    &--last {
      text-align: center;    

      > button {
        text-align: center;
        margin: 2em 0;
      }

      > a {
        text-docoration: underline;
      }
    }
  }

  &__label,
  &__header {
    font-weight: 600;
    font-size: rem(18);
    margin: rem(15) 0;
    display: block;
  }

  &__label {

    &--space-top {
      margin-top: rem(16);
    }

    > span {
      float: right;
    }
  }

  &__label-right {
    float: right;
    text-align: right;

    &--space {
      margin-left: rem(20);
    }
  }

  &__text {    
    margin-left: rem(36);
    margin-right: rem(20);
  }

  &__spacer {
    height: rem(100);
  }

  &__label-no-margin {
    margin: 0;
  }

  &__bg-transparent {
    background-color: transparent;
  }

  &__email {
    position: relative;
    transition: all .6s;

    &:before {
      content: '';
      width: 0;
      height: .0625em;
      position: absolute;
      bottom: 0;
      right: 0;
      background: $color-blue-40;
      transition: all .3s;
    }

    &:hover,
    &:focus {

      &:before {
        background: $color-blue-40;
        left: 0;
        width: 100%;
      }
    }
  }
}
