@import "../../global/utils";
@import "../helpers/colors";

.rich-text {

  h1:not([class]) {
    font-size: 3.5em; //42pt=56px=3.5em
    font-weight: 100;
    line-height: 1.25em;
    margin: 0;
    text-align: center;
    padding-top: rem(40);

    @include mq($from: xxs-screen) {
      line-height: 2em;
    }

    @include mq($from: l-screen) {
      font-size: 4.5em; //100pt=133px  130px=8.125em
      line-height: 1.25em;
    }
  }

  h2:not([class]) {
    font-size: 2.5em; //30pt=40px=2.5em
    font-weight: 600;
    line-height: 1.25em;
    color: $color-blue-40;
    text-align: center;
    margin: 0;
    padding-top: rem(30);

    @include mq($from: l-screen) {
      font-size: 3.25em; //40pt=53px 52px=3.25em
    }
  }

  h3:not([class]) {
    font-size: 2.5em; //30pt=40px=2.5em
    font-weight: 600;
    line-height: 1.425em;
    margin: 0;
    text-align: left;
  }

  h4:not([class]) {
    font-size: 2.25em; //27pt=36px=2.25em
    font-weight: 600;
    line-height: 1.875em;
    margin: 0;
    text-align: left;
  }

  h5:not([class]) {
    font-size: 1.875em; //22pt=29.33px  30px=1.875em
    font-weight: 600;
    line-height: 1.5em;
    margin: 0;
    text-align: left;
  }

  h6:not([class]) {
    font-size: 1.5em; //18pt=24px=1.5em
    font-weight: 600;
    line-height: 1.25em;
    margin: 0;
    text-align: left;

    @include mq($from: l-screen) {
      font-size: 1.875em; //30px
    }
  }

  p:not([class]) {
    font-weight: 100;
    margin-top: rem(30);

    &:empty {
      display: none;
    }

    & + h3:not([class]) {
      margin-top: rem(40);
    }

    & + h4:not([class]) {
      margin-top: rem(30);
    }

    & + h5:not([class]) {
      margin-top: rem(25);
    }

    & + h6:not([class]) {
      margin-top: rem(20);
    }

    & + p:not([class]) {
      margin-top: rem(35);
    }
  }

  ul:not([class]),
  ol:not([class]) {
    margin-bottom: 1em;
    padding: 1em 0 0 1em;

    li {
      font-size: 1em;
      font-weight: 100;
      padding-left: .625em;
    }
  }

  figcaption {
    font-size: rem(12);
    color: $color-gray-30;
  }

  blockquote:not([class]) {
    position: relative;
    margin: 1em 1.25em;
    padding: 1em;
    font: 100 rem(24)/1.4 $tdc-fontstack;
    font-style: italic;

    &:before {
      content: "";
      float: left;
      width: 1.75em;
      height: 1em;
    }

    &:after {
      content: "\201C";
      position: absolute;
      top: rem(20);
      left: rem(16);
      font-size: 2.4em;
      font-weight: 700;
      line-height: 1;
      color: $color-green-40;
    }

    @include mq($from: xs-screen) {
      padding: 1em 2em;

      &:after {
        left: rem(40);
      }
    }

    @include mq($from: m-screen) {
      font-size: rem(30);

      &:before {
        width: 1.85em;
      }

      &:after {
        top: rem(24);
        left: rem(52);
      }
    }
  }
}
