@import "../../global/utils";
@import "../helpers/colors";

@font-face {
	font-family: "TDC";
	src: url("https://i.c.dk/fonts/tdc_lt-webfont.eot");
	src: url("https://i.c.dk/fonts/tdc_lt-webfont.eot?#iefix") format("embedded-opentype"),
	url("https://i.c.dk/fonts/tdc_lt-webfont.woff") format("woff"),
	url("https://i.c.dk/fonts/tdc_lt-webfont.ttf") format("truetype");
	font-weight: 100;
	font-style: normal;
}

